@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin t-mono {
  font-family: "Space Mono", monospace;
}

@mixin margin-section-title {
  margin-bottom: 48px;

  @include onTablet {
    margin-bottom: 56px;
  }
}

@mixin t-header-title {
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;

  @include onTablet {
    font-size: 48px;
  }

  @include onDesktop {
    font-size: 56px;
  }
}

@mixin icom-width-hight {
  width: 24px;
  height: 24px;

  @include onTablet {
    width: 32px;
    height: 32px;
  }
}

@mixin logo-width-hight-phone {
  width: 125px;
  height: 16px;

  @include onTablet {
    width: 172px;
    height: 24px;
  }
}

@mixin hesderTitlePaddingBpttom {
  padding-bottom: 40px;

  @include onTablet {
    padding-bottom: 65px;
  }

  @include onDesktop() {
    padding-bottom: 92px;
  }
}

@mixin contentPaddingInline() {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 72px;
  }

  @include onDesktop {
    padding-inline: 120px;
    max-width: 1260px;
    margin-inline: auto;
  }
}

.conteiner {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 72px;
  }

  @include onDesktop {
    padding-inline: 120px;
    max-width: 1260px;
    margin-inline: auto;
  }
}

@mixin hover($property, $toVelue) {
  transition: #{$property} $transform_time;

  &:hover {
    #{$property}: $toVelue;
  }
}

@mixin marginSection-about-us {
  margin-bottom: 104px;

  @include onTablet {
    margin-bottom: 120px;
  }
}

@mixin pageGrig {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 24px;
  }

  @include onDesktop {
    --columns: 12;
  }
}

@mixin photo-center-height {
  height: 200px;

  @include onTablet {
    height: 400px;
  }

  @include onDesktop() {
    height: 460px;
  }
}
