.product {
  font-weight: 700;
  line-height: 140%;
  font-size: 16px;

  display: flex;
  flex-direction: column;
  height: 100%;

  &__photo {
    width: 100%;
    padding-bottom: 24px;

    transition: transform $transform_time;
    @include hover(transform, scale(1.05));
  }

  &__title {
    font-size: 18px;
  }

  &__category {
    padding-block: 8px 12px;
    @include t-mono;
    font-weight: 400;
  }

  &__price {
    margin-top: auto;
  }
}
