.menu {
  overflow: auto;
  height: 100vh;
  background-color: $c-nav-fon;

  &__nav {
    margin-bottom: 48px;
  }

  &__top {
    margin-bottom: 32px;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;
  }

  &__phone-number {
    margin-bottom: 16px;

    @media onTablet {
      margin-bottom: 0;
    }
  }

  &__call-to-order {
    width: fit-content;
    padding-bottom: 3px;
    border-bottom: 2px solid $c-main;
  }

  &__phone-number,
  &__call-to-order {
    display: block;

    text-transform: uppercase;
    color: inherit;
    font-size: 16px;
    font-weight: 700;
    line-height: 140%;
    letter-spacing: 2px;

  }
}
